import React, { useState, useRef, useEffect } from 'react';
import './Questions.css'; // Ensure this path matches your file structure

const Questions = ({ title, content }) => {
    const [isExpanded, setIsExpanded] = useState(false);
    const contentRef = useRef(null);

    // This effect updates whenever `isExpanded` changes.
    useEffect(() => {
        const contentCurrent = contentRef.current;
        if (isExpanded) {
            // Set max-height to the scrollHeight of the content plus a little extra space.
            // scrollHeight gives the height of the content, even if it's not currently visible.
            contentCurrent.style.maxHeight = `${contentCurrent.scrollHeight}px`;
        } else {
            contentCurrent.style.maxHeight = null;
        }
    }, [isExpanded]);

    const toggleAccordion = () => {
        setIsExpanded(!isExpanded);
    };

    return (
        <div className={`accordion-container ${isExpanded ? 'accordion-expanded' : ''}`}>
            <div className="accordion-header" onClick={toggleAccordion}>
                <h3 className="accordion-title">{title}</h3>
                <div className={`accordion-arrow ${isExpanded ? 'rotate' : ''}`}>
                    &#9660; {/* Downward arrow symbol, rotates on expand */}
                </div>
            </div>
            <div
                className="accordion-content"
                ref={contentRef}
                style={{ transition: 'max-height 0.5s ease' }}
            >
                {content}
            </div>
        </div>
    );
};

export default Questions;