import React from 'react';
import ImageContainers from './ImageContainers';
import QuemSomos_Texts from './QuemSomos_Texts';
import ContactForm from './ContactForm';
import Footer from './Footer';
import './QuemSomos.css';




const QuemSomosMain = () => {
    return (
        <div className="quem-somos" style={{ backgroundColor: "lightgrey", overflow:"hidden"}}>
            <QuemSomos_Texts />
            <ImageContainers/>
            <ContactForm />
            <Footer/>
        </div>
        );
};

export default QuemSomosMain;