import React, { useState } from 'react';
import { useTransition, animated } from 'react-spring';
import './CarouselComponent.css'; // Ensure this is correctly pointing to your CSS file

// Assuming you have corresponding content and SVGs
const carouselItems = [
    { id: 0, title: 'Service 1', description: 'Description of Service 1 ', svg: '/businessMan.svg' },
    { id: 1, title: 'Service 2', description: 'Description of Service 2', svg: '/acompanhamentoLegal.svg' },
    { id: 2, title: 'Service 3', description: 'Description of Service 3', svg: '/registroMarca.svg' },
    // Add more items as needed
];

const CarouselComponentHP = () => {
    const [index, setIndex] = useState(0);
    const [direction, setDirection] = useState('right');
    const [isAnimating, setIsAnimating] = useState(false);

    const contentTransitions = useTransition(carouselItems[index], {
        key: item => item.id,
        from: { opacity: 0, transform: direction === 'right' ? 'translate3d(100%,0,0)' : 'translate3d(-100%,0,0)' },
        enter: { opacity: 1, transform: 'translate3d(0%,0,0)' },
        leave: { opacity: 0, transform: direction === 'right' ? 'translate3d(-100%,0,0)' : 'translate3d(100%,0,0)' },
        config: { mass: 1, tension: 170, friction: 22 },
        onStart: () => setIsAnimating(true),
        onRest: () => setIsAnimating(false),
    });

    const goNext = () => {
        if (!isAnimating) {
            setDirection('right');
            setIndex(state => (state + 1) % carouselItems.length);
        }
    };

    const goPrev = () => {
        if (!isAnimating) {
            setDirection('left');
            setIndex(state => (state - 1 + carouselItems.length) % carouselItems.length);
        }
    };

    const svgTransitions = useTransition(carouselItems[index], {
        key: item => item.id,
        from: { opacity: 0 },
        enter: item => async (next) => {
            await new Promise(resolve => setTimeout(resolve, 350)); // Delay for the fade in
            await next({ opacity: 1 });
        },
        leave: { opacity: 0 },
        config: { duration: 350 },
    }); 

    return (
        <div className="carousel-wrapper-HP">
            <div className="carousel-frase-efeito">Venha fazer parte do time!</div>
            {svgTransitions((style, item) => (
                <animated.img src={`${process.env.PUBLIC_URL}/${item.svg}`} style={style} className="carousel-svg-HP" alt="icon" />
            ))}
            <div className="carousel-container-HP">
                {contentTransitions((style, item) => (
                    <animated.div className="carousel-content-HP" style={style}>
                        <div className="title-HP">{item.title}</div>
                        <div className="description-HP">{item.description}</div>
                    </animated.div>
                ))}
                <div className="arrow-HP arrow-left-HP" onClick={goPrev}>&lt;</div>
                <div className="arrow-HP arrow-right-HP" onClick={goNext}>&gt;</div>
            </div>
        </div>
    );

};

export default CarouselComponentHP;