import React from 'react';
import { useNavigate, useLocation } from 'react-router-dom';

// Custom hook for scrolling to an element
const useScrollToElement = () => {
    const location = useLocation();

    React.useEffect(() => {
        // Check if the URL hash value exists
        const hash = location.hash;
        if (hash) {
            // Extract the element ID from the hash
            const id = hash.replace('#', '');
            const element = document.getElementById(id);
            if (element) {
                // Scroll to the element
                element.scrollIntoView({ behavior: 'smooth' });
            }
        }
    }, [location]); // Re-run the effect if the hash changes
};

const ScrollLink = ({ index }) => {
    const navigate = useNavigate();
    const handleClick = (e) => {
        e.preventDefault();
        // Determine the destination path and element ID from the link
        const path = `/services`; // Change `/destination-path` to your target page's path
        const elementId = `targetElement${index + 1}`;

        // Navigate if not already on the destination page
        navigate(`${path}#${elementId}`);

        // Scroll to the element (handled by the useScrollToElement hook in the target component)
    };

    return (
        <a href={`#targetElement${index + 1}`} onClick={handleClick} className="scroll-link-services">
            Servico {index + 1}
        </a>
    );
};

const LinksContainerServices = () => {
    useScrollToElement(); // Use the custom hook to handle scrolling to elements

    return (

        <div className="links-container-services">
            {Array.from({ length: 9 }, (_, index) => (
                <ScrollLink key={index} index={index} />
            ))}
            <div className="centered-link-container">
                <a href="#special-target" className="scroll-link-services">Special Link</a>
            </div>
        </div>
        
    );
};

export default LinksContainerServices;