import React, { useState } from 'react';
import CarouselComponentHP from './CarouselComponentHP';
import PostRectangles from './PostRectangles';
import QuoteRectangles from './QuoteRectangles';
import Footer from './FooterHP';
import MainText from './MainText';

import { FaReact, FaAngular, FaVuejs, FaPython, FaJava, FaNodeJs, FaPhp, FaRust, FaSwift, FaAndroid } from 'react-icons/fa';



const HomePage = () => {

    function CompanyLogos() {
        return (
            <div className="container-marcas">
                <FaReact size={50} />
                <FaAngular size={50} />
                <FaVuejs size={50} />
                <FaPython size={50} />
                <FaJava size={50} />
                <FaNodeJs size={50} />
                <FaPhp size={50} />
                <FaRust size={50} />
                <FaSwift size={50} />
                <FaAndroid size={50} />
            </div>
        );
    }

    return (
        <div style={{ backgroundColor: 'lightgrey', overflow:'hidden' }}>
            <MainText/>
            <CarouselComponentHP />
            
            
            
            <QuoteRectangles />
            <PostRectangles />
            <h2 style={{ textAlign: "center", paddingTop: "150px" }}>Marcas parceiras</h2>
            <div >
                {CompanyLogos()}
            </div>
            <Footer/>
        </div>
        );

}

export default HomePage;