import React from 'react';
import './ContainerServicos.css'; // Make sure to import the CSS file you created

// Example component for a single rectangle
const Rectangle = ({ width, height, logo, title, subtitle, buttonLabel, services, id }) => {
    return (
        <div className="react-rectangle" id={"targetElement"+id} style={{ '--react-rectangle-width': width, '--react-rectangle-height': height }}>
            <img src={logo} alt="Logo" className="react-logo" />
            <h2 className="react-title">{title}</h2>
            <p className="react-subtitle">{subtitle}</p>
            <button className="react-button">{buttonLabel}</button>
            <ul className="react-service-list">
                {services.map((service, index) => (
                    <li key={index} className="react-service-item">
                        <span className="react-checkmark">✔</span>{service}
                    </li>
                ))}
            </ul>
        </div>
    );
};

// Main component that organizes the rectangles in a row
const RectangleRow = ({ rectangles, height }) => {
    return (
        <div className="react-rectangle-container" style={{ '--react-rectangle-spacing': '20px', paddingTop: height }}> {/* Customize the spacing here */}
      {rectangles.map((rect, index) => (
        <Rectangle
              key={index}
              width={rect.width}
              height={rect.height}
              logo={rect.logo}
              title={rect.title}
              subtitle={rect.subtitle}
              buttonLabel={rect.buttonLabel}
              services={rect.services}
              id={rect.id}
        />
      ))}
    </div>
  );
};

export default RectangleRow;